lumns:3fr 1fr 1fr;margin-bottom:4px;border-radius:4px;cursor:pointer}.song-page-body .song-page-item.active{background-color:rgba(0,0,0,.5)}.song-page-body .song-page-item:hover{background-color:#fff4f41a}.song-page-body .song-page-item:nth-last-child(1){margin-bottom:12rem}.song-page-body .song-page-item .song-item-head{height:100%;display:flex;align-items:center}.song-page-body .song-page-item .song-item-head .song-item-number{width:10%;text-align:center}.song-page-body .song-page-item .song-item-head .song-item-number h3{color:var(--text-color);font-size:1rem;font-weight:600}.song-page-body .song-page-item .song-item-head-img{height:84%;background-size:cover;background-position:center;margin:auto .5rem}.song-page-body .song-page-item .song-item-head-img img{height:100%;width:70px;aspect-ratio:3/2;object-fit:cover;border-radius:4px}.song-page-body .song-page-item .song-item-head-img .song-item-wave{position:absolute;top:calc(50% - 12.5px);left:calc(50% - 12.5px);width:25px;height:25px}.song-page-body .song-page-item .song-item-head .song-item-decoration{height:84%;margin:auto 0;float:left;padding:6px 0}.song-page-body .song-page-item .song-item-head .song-item-decoration h3{font-size:.875rem;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow:hidden;color:var(--text-color);font-weight:600;width:100%;text-align:left;margin:2px 0}.song-page-body .song-page-item .song-item-head .song-item-decoration p{width:100%;font-size:.75rem;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow:hidden;font-weight:400;color:var(--text-color);text-align:left;margin:2px 0}.song-page-body .song-page-item .song-item-head .song-item-decoration p::before{content:"Song Â· "}.song-page-body .song-page-item .song-item-time{margin:auto 0;text-align:center;font-size:.75rem;color:#999;padding:0 .5rem}.song-page-body .song-page-item .song-item-tail{margin:auto 0;text-align:center}.song-page-body .song-page-item .song-item-tail .song-item-heart{